import React from 'react';
import empty from "../../assets/empty.svg";
import { Grid, Header } from '../';

const Empty = ({ text = 'No Data', style = {}, color ="inherit"})=>{
return(
      <Grid customRows={'auto auto'} placeItems='center' width='100%'>
          <img src={empty} style={{ maxWidth: "200px", width: "100%", marginTop: "10vh" , ...style}} />
          <Header bold color={color} style={{lineHeight: "1em"}}>{text}</Header>
      </Grid>
  )
};

export default Empty;