import React, { useState } from 'react'
import { XAxis, YAxisLeft, YAxisRight, defaultAxisStyle } from '../axis';
import { useRect, DrawAreaRect } from '../../hooks/rect';
import { scaleLinear } from '@vx/scale';
import { useScale } from '../../hooks/scale';
import { useData, useCombinedData } from '../../hooks/data';
import useToolTip from '../../hooks/tooltip';
import Tooltip from '../tooltip';
import { DrawAreaProps, Graph, MultiDrawAreaProps, CombinedGraph } from '../../types';
import Grid from '../grid';
import { getChart } from '../../utils/chart';
import { useTicks } from '../../hooks/ticks';
import { AxisBottom, AxisLeft, AxisRight } from '@vx/axis';
import { TimeSeries } from '../../../../model/domain/metrics';
import { moment, getTimezone } from '../../../../utils/time';

const DrawArea = (props: DrawAreaProps) => {
  const { size, chartType, data } = props;
  const { timeSeriesResult } = data as any;
  const { height, width } = size;
  const rect = useRect({ height, width });
  const { max, points } = useData(timeSeriesResult);
  const [selected, setSelected] = useState(-1);
  const isSelected = (index: number) => selected === index;

  const { xAxis, yAxis } = useScale({ rect: rect, yMax: max, points }); 
  const tickFormatY = getYTickFormatter(timeSeriesResult);
  const tickFormatX = getXTickFormatter(timeSeriesResult);

  const { tooltipData, hideTooltip, handleTooltip, tooltipLeft, tooltipTop, tooltipOpen } = useToolTip({
    points,
    setSelected,
    units: [timeSeriesResult.unit],
    xScale: xAxis.scale,
    dateFormatter: tickFormatX
  });

  const event = (index = -1) => ({
    onTouchStart: (e: any) => handleTooltip(e, index),
    onTouchMove: (e: any) => handleTooltip(e, index),
    onMouseMove: (e: any) => handleTooltip(e, index),
    onMouseLeave: () => {
      setSelected(-1);
      hideTooltip();
    }
  })

  const chartProps: Graph = {
    yScale: yAxis.scale,
    points,
    xScale: xAxis.scale,
    isSelected,
    noneSelected: selected === -1,
    event
  };

  return (
    <div style={{ height: height + 'px', width: '100%', position: 'relative' }}>
      <SVG height={height} width={'100%'} {...event(-1)}>
        <Tooltip.Line
          height={height}
          tooltipData={tooltipData}
          tooltipLeft={tooltipLeft} 
        />

          <g transform={`translate(0,${rect.height - rect.margin.bottom})`}>
            <XAxis
              scale={xAxis.scale}
              tickFormat={tickFormatX}
              axisStyleProps={defaultAxisStyle} />
          </g>

          <g transform={`translate(${rect.margin.left},0)`}>
            <Grid yScale={yAxis.scale} width={rect.innerWidth} />
            <YAxisLeft
              scale={yAxis.scale}
              tickFormat={tickFormatY}
              axisStyleProps={defaultAxisStyle} />
          </g>

          {/*always have this below the <Grid />*/}
          {getChart(chartType, chartProps)}

      </SVG>
      <Tooltip
        tooltipOpen={tooltipOpen}
        tooltipLeft={tooltipLeft}
        tooltipTop={tooltipTop}
        tooltipData={tooltipData}
        isSelected={isSelected}
        noneSelected={selected === -1}
      />
    </div>
  );
}

const MultiDrawArea = (props: MultiDrawAreaProps) => {
  const { size, chartType, datas } = props;
  const [{ timeSeriesResult }, { timeSeriesResult: timeSeriesResult2 }] = datas;
  const { height, width } = size;
  const rect = useRect({ height, width });
  const data1 = useData(timeSeriesResult);
  const data2 = useData(timeSeriesResult2);
  const dataComb = useCombinedData([timeSeriesResult, timeSeriesResult2])
  
  const { xAxis, yAxis } = useScale({
    rect,
    yMax: data1.max,
    points: data1.points
  });

  console.log('rerendering');
  
  const { yAxis: y2Axis } = useScale({
    rect,
    yMax: data2.max,
    points: data2.points
  });

  const tickFormatYLeft = getYTickFormatter(timeSeriesResult);
  const tickFormatYRight = getYTickFormatter(timeSeriesResult2);
  const tickFormatX = getXTickFormatter(timeSeriesResult);

  const { tooltipData, hideTooltip, handleTooltip, tooltipLeft, tooltipTop, tooltipOpen } = useToolTip({
    points: dataComb.points,
    units: [timeSeriesResult.unit, ''],
    setSelected: () => { },
    xScale: xAxis.scale,
    dateFormatter: tickFormatX
  });
  
  const event = ({
    onTouchStart: (e: any) => handleTooltip(e, -1),
    onTouchMove: (e: any) => handleTooltip(e, -1),
    onMouseMove: (e: any) => handleTooltip(e, -1),
    onMouseLeave: () => hideTooltip()
  });
  
  const chartProps: CombinedGraph = {
    yScale: yAxis.scale,
    points: data1.points,
    points2: data2.points,
    y2Scale: y2Axis.scale,
    xScale: xAxis.scale,
  };
  
  return (
    <div style={{ height: height + 'px', width: '100%', position: 'relative' }}>
      <SVG height={height} width={'100%'} {...event}>
        <Tooltip.Line
          height={height}
          tooltipData={tooltipData}
          tooltipLeft={tooltipLeft} />

        <g transform={`translate(0,${rect.height - rect.margin.bottom})`}>
            <XAxis
              scale={xAxis.scale}
              tickFormat={tickFormatX}
              axisStyleProps={defaultAxisStyle} />
        </g>
     
        <g transform={`translate(${rect.margin.left},0)`}>
          <Grid yScale={yAxis.scale} width={rect.innerWidth} />
          <YAxisLeft
              scale={yAxis.scale}
              tickFormat={tickFormatYLeft}
              axisStyleProps={defaultAxisStyle} />
        </g>

        <g transform={`translate(${rect.width - rect.margin.right},0)`}>
          <YAxisRight
            scale={y2Axis.scale}
            tickFormat={tickFormatYRight}
            axisStyleProps={defaultAxisStyle} />
        </g>

          {/*always have this below the <Grid />*/}
          {getChart(chartType, chartProps)}

      </SVG>
      <Tooltip
        tooltipOpen={tooltipOpen}
        tooltipLeft={tooltipLeft}
        tooltipTop={tooltipTop}
        tooltipData={tooltipData} />
    </div>
  );
}

interface SVGProps {
  height: number | string,
  width: number | string,
  children: any,
  onTouchStart: {(e: any): void};
  onTouchMove: {(e: any): void};
  onMouseMove: {(e: any): void};
  onMouseLeave: {(e: any): void};
}

const SVG = (o: SVGProps) => (
  <svg width={o.width} height={o.height}>
    {o.children}
    <rect
      x={0}
      y={0}
      width={o.width}
      height={o.height}
      fill={'transparent'}
      onTouchStart={o.onTouchStart}
      onTouchMove={o.onTouchMove}
      onMouseMove={o.onMouseMove}
      onMouseLeave={o.onMouseLeave} />
  </svg>
);

function identity(i: any) { return i; }

DrawArea.Multi = MultiDrawArea;
export default DrawArea;

function getYTickFormatter(ts: TimeSeries.Result) {
  const identity = (n: any) => n;
  const centsFormatter = (n: number) => (n / 100).toFixed(0) + '$';
  const percentFormatter = (n: number) => (n * 100).toFixed(1) + '%';

  return ts.unit === '%'
    ? percentFormatter
    : ts.unit === 'cents'
    ? centsFormatter
    : identity;
}

const MONTHLY_ALIGNMENT = 1440 * 29;
const WEEKLY_ALIGNMENT = 1440 * 7;
function getXTickFormatter(ts: TimeSeries.Result) {
  const f = (date: string, formatter: string) =>
    moment.tz(date, getTimezone()).format(formatter);

  //My bread and butter is monthly
  const formatterA = (d: string) => {
    return `${f(d, 'MMM')}`
  }

  //I go bananas with weekly
  const formatterB = (d: string) => {
    const d0 = moment(d).subtract(1, 'week').toISOString();
    return `${f(d0, 'MMM DD')} - ${f(d, 'MMM DD')}`
  }

  //I am abused the most
  const formatterC = (()=> {
    const points = ts.timeSeries[0].points;

    const e = moment(points[0].at);
    const s = moment(points[points.length - 1].at);

    const dayDiff = e.diff(s, 'days', true);
    const monthDiff = e.diff(s, 'months', true);

    const formatString =  dayDiff <= 1
      ? 'h:mm A'
      : dayDiff <= 7
        ? 'ddd h A'
        : monthDiff <= 1
        ? 'MMM DD'
        : null;

    let isFirst = true;
    const mmmFormatter = (d: string) => {
      const m = moment.tz(d, getTimezone());

      if (isFirst || m.get('month') === 0) {
        isFirst = false;
        return f(d, 'MMM, YYYY');
      }

      return f(d, 'MMM DD');
    }

    return (d: string) => formatString === null? mmmFormatter(d): f(d, formatString);
  })();

  // >= on Monthly coz, api might return 29, 30 or 31 (perhaps)
  return ts.alignmentPeriod >= MONTHLY_ALIGNMENT
    ? formatterA
    : ts.alignmentPeriod === WEEKLY_ALIGNMENT
    ? formatterB
    : formatterC;
}