import React from "react";
import { Header, Grid, Button, Notification } from "../../../style";
import { primaryColor } from "../../../config";
import { overflow } from "../../../utils/text";
import {  Charts } from "../types";
import charts from '../json/charts.json'

const ChartHeader = ({ chartType, chartTypes, setChartType, title, filter }: any)=>(
    <Header
    className="grid"
    color={primaryColor}
    style={{
      gridColumn: "1/-1",
      width: "100%",
      gridTemplateColumns: "1fr auto auto",
      placeItems: "start ",
    }}
    bold
    size={4}
  >
    <Grid mobileLayout={false} customCols="auto 1fr auto" gridGap=".5em" padding=".2em .5em"
      width={'100%'}
      style={{
        placeItems: 'start',
        borderRadius: "1em",
        textTransform: "capitalize",
        placeSelf: "start",
      }}
    >
      <Header bold size={3.75} textAlign='left'>{overflow(title, 25)}</Header>
      <div style={{ placeSelf: "end" }}>
        <Button
          invert
          minWidth="3em"
          margin='0 1em'
          noBorder={true}
          onClick={() => Notification.success({ title: 'Success', message: 'Exporting Data to Excel' })}
          faIcon={'fas fa-file-download'}
          tooltip='Download the current data on Excel'
        />
        {
           charts.map(({ key, faIcon, label }) => (
            chartTypes && <span key={key}>{chartTypes.includes(key) &&
            <Button
              invert={chartType !== key}
              minWidth="3em"
              noBorder={chartType !== key}
              onClick={() => setChartType(key as Charts)}
              faIcon={faIcon}
              tooltip={label}
            />}
            </span>
          ))
        }
      </div>
      {filter}
    </Grid>
  </Header>
  );
export default ChartHeader;