import React from 'react'
import { BarStack } from '@vx/shape';
import { scaleOrdinal } from '@vx/scale';
import { colors } from '../../json/colors.json';
import { Group } from '@vx/group';
import { getColor } from '../../utils/color';
import { Graph } from '../../types';
import { getKeys } from '../../utils/points';

export function StackedBar({ yScale, points, xScale }: Graph) {
  const keys = getKeys(points);
  const getDate = (d: any) => (new Date(d?.date)).toISOString();

  const colorScale = scaleOrdinal<string, string>({
    domain: keys,
    range: colors,
  });

  return (
    <Group >
      <BarStack<any, string>
        keys={keys}
        data={points}
        xScale={xScale}
        yScale={yScale}
        color={colorScale}
        x={getDate}
      >
        {barStacks =>
          barStacks.map(barStack => {
            const index = barStack.index;
            return barStack.bars.map((bar) => {
              if (bar.bar.data[bar.key] === '-1') return null;
              return (
                <rect
                  key={`bar-stack-${barStack.index}-${bar.index}`}
                  x={bar.x}
                  y={bar.y}
                  opacity={0.6}
                  height={bar.height}
                  width={bar.width}
                  fill={getColor(index)}
                />
              )
            })
          }
          )}
      </BarStack>
    </Group>);
};

export default StackedBar;