import React, { useState, useEffect } from 'react';
import { useSelector } from 'react-redux';
import { Grid, Button } from '../../style';
import Filter from '../garage/analytics/filter';
import { useMetricData } from '../../hooks/metrics';
import useTimeseries from '../../hooks/timeseries';
import options from './filter-options';
import chart from './chart.json';
import Chart from '../../components/chart';
import { currentMonth } from '../../utils/time';
import { Select } from 'antd';

const revenueTimeSeries = {...chart.revenueTimeSeriesQuery, interval: currentMonth()};
const countTimeSeries = { ...chart.transactionCountTimeSeriesQuery };

const Analytics = ({ filterWidth, open, setOpen }: any) => {
    const eId = useSelector((state: any) => state.user.enterprise.id);
    const [toggle, setToggle] = useState(false);
    const revenueMetric = useMetricData({
        timeseries: revenueTimeSeries,
        interval: currentMonth(),
        eId
    });

    const countMetric = useMetricData({
        timeseries: countTimeSeries,
        interval: currentMonth(),
        eId
    });

    const { timeseries, actions } = useTimeseries(revenueTimeSeries as any);

    const filterProps = {
        defaultValue: timeseries,
        metricDataStatus: revenueMetric.metricDataStatus,
        filterWidth,
        loading: revenueMetric.metricDataStatus.loading || countMetric.metricDataStatus.loading,
        submit: () => {
            revenueMetric.refresh(timeseries);
            countMetric.refresh({ ...timeseries, metricType: "citation_transaction_count" })
        },
        reset: () => {
            revenueMetric.refresh({ ...countTimeSeries, interval: currentMonth() })
            countMetric.refresh({ ...revenueTimeSeries, interval: currentMonth() })
        },
        actions,
        hideTimeSelection: true,
        timeseries,
        options: { ...options, aggregations: undefined }
    }

    useEffect(() => {
        if (toggle) {
            revenueMetric.refresh(timeseries);
            countMetric.refresh({ ...timeseries, metricType: "citation_transaction_count" })
            setToggle(false)
        }
    }, [timeseries]);


    const selectProps = {
        style: { width: '100px', marginRight: "1em" },
        disabled: revenueMetric.metricDataStatus.loading || countMetric.metricDataStatus.loading
    };
    return (
        <Grid placeItems="start stretch" padding="1em" height="100%">
            <Grid cols="1" gridGap="0 1em">
                <Chart.Multi
                    className='slide-up'
                    filterWidth={filterWidth}
                    metricDataStatuses={[revenueMetric.metricDataStatus, countMetric.metricDataStatus]}
                    {...chart as any}
                    legend={{ label: 'Citations' }}
                    filter={
                        <AdditionalFilter
                            actions={actions}
                            open={open}
                            selectProps={selectProps}
                            setToggle={setToggle}
                            setOpen={setOpen}
                        />}
                />
                <Filter {...filterProps} open={open} toggle={() => setOpen(false)} />
            </Grid>
        </Grid>

    );
}

const AdditionalFilter = ({ actions, open, selectProps, setToggle, setOpen }: any) => (
    <div>
        <Select
            onChange={(e: any) => {
                actions.setAlignment(JSON.parse(e));
                setToggle(true);
            }}
            defaultValue={"Daily"}
            {...selectProps}
        >
            {options.aggregations.values.map(({ value, label }: any) => <Select.Option value={JSON.stringify(value)}>
                {label}
            </Select.Option>)}
        </Select>
        {!open ?
            <Button
                placeSelf="end"
                rounded
                shadow
                minWidth="3em"
                onClick={() => setOpen(!open)}
                faIcon={`fa fa-filter`}
            /> : null}</div>
)

export default Analytics;