import React, { useState } from 'react';
import Layout from '../../layout';
import ScoreCards from '../../components/score-card/score-cards';
import { useWidgets } from '../../hooks/widget';
import { useSelector } from 'react-redux';
import { Grid } from '../../style';
import Analytics from './analytics';

const Citations = () => {
    const eId = useSelector((state: any) => state.user.enterprise.id);
    const { loading, scoreCards } = useWidgets({ eId, screen: "citation" });
    const [open, setOpen] = useState(true);
    const shouldPad = window.innerWidth > 1360;
    const filterWidth = open ? 375 : 0;

    return (
        <Layout sidebar>
            <Grid customCols="1fr auto" width="100%" gridGap="0" placeItems="start" style={{ height: "100%" }}>
                <div style={{ width: "100%", placeSelf: 'start end' }}>
                    <Layout.Top style={shouldPad ? { margin: '0 0 0 8%', maxWidth: '100%' } : {}}>
                        <div></div>
                        <ScoreCards
                            loading={loading}
                            eId={eId}
                            scoreCards={scoreCards}
                        />
                    </Layout.Top>
                    <Layout.Bottom style={shouldPad ?
                        { margin: '-10em 0 0 8%', maxWidth: '100%' } :
                        { margin: '-10em auto auto auto', maxWidth: '1500px' }}
                    >
                        <Analytics open={open} setOpen={setOpen} filterWidth={filterWidth} />
                    </Layout.Bottom>
                </div>
                <div className='overlay-tablet' style={{ width: filterWidth }}></div>
            </Grid>
        </Layout>
    );
}

export default Citations;